import React from 'react';
import { useAuth } from '../../contexts/AuthContext';
import type { UserRole } from '../../types';

interface ProfileField {
  label: string;
  value: string;
}

interface RoleSpecificFields {
  fields: ProfileField[];
}

const getRoleSpecificFields = (role: UserRole | undefined): RoleSpecificFields => {
  switch (role) {
    case 'student':
      return {
        fields: [
          { label: 'Program', value: 'Informatics (S1)' },
          { label: 'Current Semester', value: '5' },
          { label: 'Academic Status', value: 'Active' },
          { label: 'Advisor', value: 'Dr. Ahmad Rahman' },
        ]
      };
    case 'admin':
      return {
        fields: [
          { label: 'Unit', value: 'Academic Office' },
          { label: 'Permissions', value: 'Admin (Full Access)' },
        ]
      };
    case 'lecturer':
      return {
        fields: [
          { label: 'Department', value: 'Informatics' },
          { label: 'Academic Rank', value: 'Assistant Professor' },
        ]
      };
    default:
      return { fields: [] };
  }
};

const Profile: React.FC = () => {
  const { user } = useAuth();
  const roleSpecificFields = getRoleSpecificFields(user?.role as UserRole);

  return (
    <div className="max-w-3xl mx-auto space-y-6">
      <h1 className="text-2xl font-bold text-gray-900">Profile</h1>

      <div className="card">
        <div className="flex items-center space-x-4">
          <div className="h-16 w-16 rounded-full bg-primary-600 text-white flex items-center justify-center text-xl font-semibold">
            {user?.name?.[0] || 'U'}
          </div>
          <div>
            <div className="text-lg font-semibold">{user?.name}</div>
            <div className="text-sm text-gray-500">{user?.email}</div>
            {user?.studentId && (
              <div className="text-sm text-gray-500">NIM: {user.studentId}</div>
            )}
            {user?.employeeId && (
              <div className="text-sm text-gray-500">NIP: {user.employeeId}</div>
            )}
          </div>
        </div>
      </div>

      {roleSpecificFields.fields.length > 0 && (
        <div className="card grid grid-cols-1 md:grid-cols-2 gap-4">
          {roleSpecificFields.fields.map((field, index) => (
            <div key={index}>
              <div className="text-sm text-gray-500">{field.label}</div>
              <div className="font-medium">{field.value}</div>
            </div>
          ))}
        </div>
      )}
    </div>
  );
};

export default Profile;
